/*
* Style Works for Editor Only
*/

// Desktop Styles
@media (min-width: 1025px) {
	.pinit-container {
		display: grid;
		grid-template-columns: 3fr 2fr;
		background: #f8f3f3;
		border-radius: 15px;
		padding: 30px 30px 0px 30px !important;
		margin-bottom: 20px;
		background-image: url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png);
		background-position: top right;
		background-size: auto 370px;
		.pinit-left {
			display: grid;
			align-items: center;
			.flexguten-pinit-card-title {
				font-weight: 900;
				line-height: 1.3;
				font-family: "Merriweather", sans-serif;
				text-decoration: none;
			}

			.flexguten-pinit-card-subtitle {
				font-family: montserrat, Arial;
				letter-spacing: 0.5px;
				line-height: 1.65em;
			}

			.pinterest-button {
				background-image: url(/includes/assets/images/pin-btn.png);
				align-items: center;
				background-color: #fff;
				background-size: 37px;
				background-repeat: no-repeat;
				border-radius: 20px;
				display: inline-flex;
				font-size: 13px;
				font-weight: 700;
				height: 36px;
				letter-spacing: 1.1px;
				text-transform: uppercase;
				padding: 0 20px 0px 40px;
				color: #333333;
				text-decoration: none;
				& svg {
					height: 17px;
					margin-left: 10px;
				}
			}
		}

		.pinit-right {
			align-items: center;
			display: grid;

			.review-image-button {
				display: flex;
				justify-content: center;
			}

			.review-image {
				width: 100%;
				height: 100%;
				display: grid;
				align-items: end;

				& img {
					object-fit: cover;
					-o-object-fit: cover;
					display: grid;
					justify-self: center;
					margin-top: 10px;
					border: 5px solid white;
					border-radius: 15px 15px 0px 0px;
					object-position: center;
					transform: translateY(0);
					transition: transform 0.2s, -webkit-transform 0.2s !important;
					will-change: transform;
					margin-bottom: -15px;
				}
			}
		}
	}

	.pinit-container:hover .review-image {
		transform: translateY(-10px);
		transition: 0.5s;
	}
}

// Tablet Styles

