p.flexguten-label {
	font-weight: 500;
}

.flexguten-tab-panel {
	border-top: 1px solid #ccc;

	.components-tab-panel__tabs-item:focus:not(:disabled) {
		box-shadow: none;
	}

	.components-panel__body-toggle.components-button:focus {
		box-shadow: none;
	}

	.components-button:focus:not(:disabled) {
		box-shadow: none;
	}

	& .flexguten-general {
		display: block;
		width: 100%;

		&.flexguten-active {
			background: #ed95a0;
			color: white;
			border-top: 3px solid #e60023;
		}
	}
}
