.flexguten-margin-padding-section {
	.flexguten-section-label {
		font-size: 11px;
		font-weight: 500;
		line-height: 1.4;
		text-transform: uppercase;
		display: inline-block;
		margin-bottom: calc(15px);
	}
	.flexguten-border-main {
		display: grid;
		grid-template-columns: repeat(4, 1fr);

		.border-width {
			border: 1px solid #ccc;
			background: #eee;
			margin-right: 3px;
			border-radius: 5px;
			padding: 3px;
			display: grid;
			justify-content: center;

			.flexguten-label {
				text-align: center;
				margin-bottom: 2px;
				text-transform: uppercase;
				font-size: 8px;
			}

			.number-input-box {
				width: 38px;
			}
		}
	}
}
